#progress {
  position: fixed;
  bottom: 20px;
  right: 10px;
  height: 64px;
  width: 64px;
  display: grid;
  place-items: center;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
#progress-value {
  display: block;
  height: calc(100% - 15px);
  width: calc(100% - 15px);
  background-color: #4f2ee8;
  border-radius: 50%;
  display: grid;
  place-items: center;
}

.scroll-icon {
  fill: #fff;
}
