.support-container {
  position: relative;
  background: radial-gradient(
    183.6% 183.01% at 59.76% -23.42%,
    #4f2ee8 18.03%,
    #ffffff 100%
  );
  border-radius: 17px;
  width: 335px;
  height: 352px;
}
.sup-content-wrp {
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 188px;
  padding-left: 40px;
  overflow: auto;
}
@media only screen and (min-width: 768px) {
  .sup-content-wrp {
    height: 292px;
  }
}
@media only screen and (min-width: 1440px) {
  .sup-content-wrp {
    height: 292px;
  }
}
@media only screen and (min-width: 768px) {
  .support-container {
    width: 335px;
    height: 472px;
  }
}
@media only screen and (min-width: 1440px) {
  .support-container {
    width: 356px;
    height: 490px;
  }
}
.sup-ukr-header {
  display: flex;
  gap: 12px;
  margin-bottom: 40px;
  padding-left: 40px;
  padding-left: 40px;
  padding-top: 24px;
}
.sup-ukr-text {
  font-style: normal;
  color: var(--white-color);
  font-size: 28px;
  line-height: 1.14;
}
.sup-ukr-gerb {
  color: var(--white-color);
  fill: currentColor;
}
.support-company {
  width: 150px;
  display: flex;
  align-items: center;
  gap: 16px;
  color: var(--white-color);
}
.sup-comp-numb {
  line-height: 1.28;
  font-weight: 400;
  color: var(--white-color);
}
.sup-company-img {
  object-fit: contain;
  height: 32px;
}
.sup-ukr-scroll-btn {
  width: 32px;
  height: 32px;
  background-color: var(--white-color);
  border-radius: 50%;
}
.sub-btn-svg {
  color: var(--blue-color);
  fill: currentColor;
  border-radius: 50%;
  width: 11.92px;
  height: 7.98px;
}
.btn-wrapper {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}
